/* eslint-disable @typescript-eslint/no-unused-vars */
import BadgeTwoToneIcon from '@mui/icons-material/BadgeTwoTone'
import FactCheckTwoToneIcon from '@mui/icons-material/FactCheckTwoTone'
import ScreenSearchDesktopTwoToneIcon from '@mui/icons-material/ScreenSearchDesktopTwoTone'
import { Box, Card, CardContent, Container, Stack, styled, Typography, useMediaQuery, useTheme } from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { ReactComponent } from 'src/assets/undraw_about_us_page.svg'
import ClubList from 'src/components/ClubList'

const IndexImage = styled(ReactComponent)(({ theme }) => ({
  width: '100%'
}))

interface ApplyingClubType {
  id: number
  name: string
  type: string
  category: string
  location: string
}

function Index() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const [count, setCount] = useState(matches ? 20 : 10)
  const [clubs, setClubs] = useState(null)
  const { enqueueSnackbar } = useSnackbar()

  useEffect(() => {
    axios
      .get<ApplyingClubType[]>(
        `${process.env.REACT_APP_APIHOST}/apply/clubs?page=${encodeURIComponent('1')}&count=${encodeURIComponent(
          `${count}`
        )}`
      )
      .then(res => {
        if (res.data?.length > 0) setClubs(res.data)
        else enqueueSnackbar('해당하는 동아리가 없습니다', { variant: 'warning' })
      })
      .catch(err => enqueueSnackbar(err.response.statusText, { variant: 'error' }))
  }, [count])

  return (
    <>
      <Box
        width="100%"
        sx={{
          backgroundColor: 'white',
          display: 'flex',
          flexDirection: matches ? 'row' : 'column',
          justifyContent: 'space-around',
          alignItems: 'center',
          padding: theme.spacing(8, 0),
          [theme.breakpoints.down('md')]: {
            padding: theme.spacing(5, 0)
          }
        }}
      >
        <Container maxWidth="xs" sx={{ paddingBottom: matches ? 0 : 3 }}>
          <Typography variant={matches ? 'h2' : 'h3'} component="h1" gutterBottom>
            All the Clubs on KLUB!
          </Typography>
          <Typography variant="subtitle2" component="p">
            KLUB은 고려대학교 동아리를 위한 웹 서비스입니다. 카테고리와 소속 단위에 따라 어떤 동아리가 있는지
            확인해보세요. 동아리 운영진이라면 KLUB만의 획기적인 실시간 회원 관리 툴을 접해보세요!
          </Typography>
        </Container>
        <Container maxWidth="xs">
          <IndexImage />
        </Container>
      </Box>
      <Container
        maxWidth="md"
        sx={{
          padding: theme.spacing(5, 0),
          [theme.breakpoints.up('sm')]: {
            padding: theme.spacing(8, 0)
          }
        }}
      >
        <Stack
          direction={matches ? 'row' : 'column'}
          spacing={4}
          justifyContent="center"
          sx={{ padding: theme.spacing(0, matches ? 0 : 7) }}
        >
          <Card sx={{ width: matches ? '20%' : '100%', backgroundColor: theme.colors.primary.lighter, padding: 1 }}>
            <CardContent>
              <Box width="100%" display={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <ScreenSearchDesktopTwoToneIcon fontSize="large" sx={{ marginBottom: 1 }} />
              </Box>
              <Typography variant="h6" component="h3" sx={{ textAlign: 'center' }}>
                편리한 동아리 검색
              </Typography>
            </CardContent>
          </Card>
          <Card sx={{ width: matches ? '20%' : '100%', backgroundColor: theme.colors.primary.lighter, padding: 1 }}>
            <CardContent>
              <Box width="100%" display={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <BadgeTwoToneIcon fontSize="large" sx={{ marginBottom: 1 }} />
              </Box>
              <Typography variant="h6" component="h3" sx={{ textAlign: 'center' }}>
                모집과 홍보
              </Typography>
            </CardContent>
          </Card>
          <Card sx={{ width: matches ? '20%' : '100%', backgroundColor: theme.colors.primary.lighter, padding: 1 }}>
            <CardContent>
              <Box width="100%" display={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <FactCheckTwoToneIcon fontSize="large" sx={{ marginBottom: 1 }} />
              </Box>
              <Typography variant="h6" component="h3" sx={{ textAlign: 'center' }}>
                회원 명단 관리
              </Typography>
            </CardContent>
          </Card>
        </Stack>
      </Container>
      <Container sx={{ padding: theme.spacing(2, matches ? 0 : 2) }}>
        <Typography variant="h3" component="h2" gutterBottom sx={{ paddingBottom: 1 }}>
          현재 모집 중인 동아리예요
        </Typography>
        <ClubList clubs={clubs} />
      </Container>
    </>
  )
}

export default Index
